<template>
  <div class="alert-img">
        <mt-popup
            v-if="init.key"
            v-model="init.key"
            popup-transition="popup-fade"
            :style="style"
            @click.native="closePup"
            >
            <div class="img-box">
                <mt-swipe :auto="0" :defaultIndex="init.index" @change="change">
                    <mt-swipe-item v-for="(item,i) in imgArr" :key="i" :data-index="i" :data-id="item.id" :ref="'img'+i">
                        <img :ref="'imgr'" class="img" :src="item.dataurl==''?item.url:item.dataurl" :alt="item.alt" @load="autoImg">
                    </mt-swipe-item>
                </mt-swipe>
            </div>
            <div class="img-btn">
                <button :data-index="init.index" :data-id="init.id" @click.stop="remove($event)"></button>
                <button v-if="autobutton" :data-index="init.index" :data-id="init.id" @click.stop="set($event)">设置为封面</button>
                <!-- <button @click.stop="al($event)">弹出</button> -->
            </div>
        </mt-popup>
  </div>
</template>
<script>
import { Swipe, SwipeItem } from 'mint-ui';
import com from '../../assets/common.js';
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name, SwipeItem);
    export default {
        created(){
        },
        data(){
            return{
                //设置css
                style:{
                    "backgroundColor":"#333",
                    height:"100%",
                    width:'100%'
                },
                id:''
            }
        },
        methods:{
            //改变id
            change(i){
                this.init.id=this.$refs['img'+i][0].$attrs['data-id'];
                this.init.index=this.$refs['img'+i][0].$attrs['data-index'];
                this.autoImg();
            },
            //关闭弹窗
            closePup(){
                this.init.key=false;
            },
            stext(arr,obj){
                for(let i=0;i<arr.length;i++){
                    if(arr[i].id==obj){
                        return arr[i];
                    }
                }
            },
            //删除图片
            remove(ev){
                let id=ev.target.dataset.id;
                let index=com.contains(this.imgArr,id,'id');
                this.imgArr.splice(index,1);
                this.init.key=false;
                console.log(id);
                if(this.imgArr.length==0){
                    this.$emit('remove');
                }else{
                    this.init.id=this.imgArr[0].id;
                    this.init.url=this.imgArr[0].url;
                    this.init.index=0;
                }
            },
            //设置图片
            set(ev){
                let id=ev.target.dataset.id;
                let index=ev.target.dataset.index;
                this.$emit('indexChange',index);
                this.init.key=false;
                let thatObj=this.stext(this.imgArr,id);
                this.init.url=thatObj.url;
                console.log(id);
            },
            autoImg(){
                let imgPic=this.$refs.imgr;
                //console.log(imgPic);
                let docW=$(window).width();
                //console.log(docW);
                for(let i in imgPic){
                    //console.log(imgPic[i]);
                    try {
                        if(imgPic[i].clientHeight==imgPic[i].clientWidth){
                            imgPic[i].style.width=docW+"px";
                            imgPic[i].style.height="auto";
                        }else if(imgPic[i].clientHeight>imgPic[i].clientWidth){
                            imgPic[i].style.width=(320*imgPic[i].clientWidth)/imgPic[i].clientHeight+"px";
                            imgPic[i].style.marginLeft=(docW-((320*imgPic[i].clientWidth)/imgPic[i].clientHeight))/2+"px";
                            imgPic[i].style.height="320px";
                        }else{
                            imgPic[i].style.width=docW+"px";
                            imgPic[i].style.height=(320*imgPic[i].clientHeight)/imgPic[i].clientWidth+"px";
                            imgPic[i].style.marginTop=(320-(320*imgPic[i].clientHeight)/imgPic[i].clientWidth)/2+"px";
                        }
                    } catch (error) {
                        
                    }
                }
            }
        },
        props:['init','imgArr','autobutton'],
        mounted(){
            let that=this;
        }
    }
</script>
<style scoped lang="scss">
.alert-img {
    position: relative;
  }
  .img-box {
    height: 70%;
    width: 100%;
    min-width:320px;
    max-width:640px;
    position: absolute;
    top: 15%;
  }
  .img-box .img {
    display: block;
  }
  .img-btn {
    display: flex;
    padding: 0 5%;
    justify-content: space-between;
    flex-direction: row;
    position: absolute;
    bottom: 20px;
    width: 90%;
  }
  .img-btn button {
    outline: none;
  }
  .img-btn button:first-child {
    width: 30px;
    height: 30px;
    display: block;
    background: #111111 url(../../assets/icon-ljt.png) no-repeat center;
    background-size: 15px;
    border: 0;
    border-radius: 30px;
  }
  .img-btn button:first-child:active,
  .img-btn button:last-child:active {
    background-color: #000;
  }
  .img-btn button:last-child {
    height: 26px;
    display: block;
    font-size: 14px;
    color: #fff;
    margin: 3px 0;
    padding: 0 15px;
    border: 1px solid #fff;
    border-radius: 5px;
    background-color: transparent;
  }
  
</style>



